// --------------------------------------------
// This file contains main grid styles
// --------------------------------------------

.App {
  display: grid;
  width: 100%;
  height: auto;
  grid-template-columns: 60% 40%;
}
.containerAll {
  display: grid;
  grid-template-columns: 100%;
}

.containerRight {
  display: grid;
  grid-template-columns: 50% 50%;
}

.wrapperAll {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-columns: 33% 33% 33%;
}

.item {
  border-radius: 3px;
  align-self: stretch;
}

