@media (max-width: 1500px) {
  .App {
    display: grid;
    width: 100%;
    height: auto;
    grid-template-columns: 100%;
  }
  .containerAll {
    display: grid;
    height: auto;
    grid-template-columns: 100%;
  }

  .containerRight {
    display: grid;
    width: 100%;
    height: auto;
    grid-template-columns: 50% 50%;
  }

  .wrapperAll {
    display: grid;
    width: 100%;
    height: auto;
    grid-template-columns: 33% 33% 33%;
  }
}

@media (max-width: 800px) {
  .App {
    display: grid;
    width: 100%;
    height: auto;
    grid-template-columns: 100%;
  }

  .containerAll {
    display: grid;
    grid-template-columns: 100%;
    width: 100%;
    height: auto;
  }

  .containerRight {
    display: grid;
    grid-template-columns: 100%;
    width: 100%;
    height: auto;
  }

  .wrapperAll {
    display: grid;
    width: 100%;
    height: 100%;
    grid-template-columns: 100%;
  }

  div.peddeestal {
    margin-top: 110px;
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
    max-width: 300px;
  }

  .peddeestal div.stage {
    width: 100px;
  }
}
